import { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faRectangleXmark, faXmark } from '@fortawesome/free-solid-svg-icons';
import cls from '../Navbar.module.scss';
import { Link, useNavigate } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
import { FLOWHERO } from '../../../../shared/utility';
import { resetApplication } from '../../../../services/auth.service';
import { Button } from '../../../common';

export const Navbar = props => {
	const { background = 'white' } = props;
	const [nav, setNav] = useState(false);
	const navigate = useNavigate();


	const pricingRef = document.getElementById('ourpricing')

	const openNav = () => {
		setNav(!nav);
	};

	return (
		<div className={cls.navbarsection} style={{ position: 'fixed', backgroundColor: background }}>
			<div className={cls.navbartitle} style={{ fontSize: isMobile ? '20px' : '26px', marginLeft: isMobile ? '0' : '50px' }}>
				{FLOWHERO(isMobile ? '26px' : '28px', 'var(--app-color-gray-dark)', 'bold')}
			</div>
			{/* Desktop */}
			<ul className={cls.navbaritems} style={{ display: isMobile ? 'none' : '' }}>
				<li>
					<Link to="/" className={cls.navbarlinks}>
						{`ABOUT US`}
					</Link>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer' }}
						onClick={() => {
							//pricingRef.scrollIntoView({ behavior: 'smooth' });
							window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' })
						}}
					>
						{'PRICING'}
					</a>
				</li>
				<li>
					<a href="#/featured/sports" className={cls.navbarlinks}>
						{`PALARO`}
					</a>
				</li>
				<li>
					<a href="#/register" className={cls.navbarlinks}>
						{`REGISTER`}
					</a>
				</li>
				<li>
					<Button title={'LOGIN'}
						style={{ marginTop: '20px', backgroundColor: background }}
						titleStyle={{
							fontFamily: 'var(--app-text-main-font)',
							color: 'black',
							fontWeight: 'bold',
							fontSize: '14px',
							letterSpacing: '.8px'

						}}
						light
						clicked={() => {
							resetApplication();
							navigate('/login');
						}}
					/>

				</li>
			</ul>

			{/* Mobile */}
			<div className={`mobile-navbar ${nav ? 'open-nav' : ''}`} style={{height: '60%'}}>
				<div onClick={openNav} className="mobile-navbar-close">
					<FontAwesomeIcon icon={faRectangleXmark} className="hamb-icon" style={{ width: '30px', height: `30px` }} />
				</div>

				<ul className={cls.mobilenavbarlinks}>
					<li>
						<Link onClick={openNav} to="/">
							{`HOME`}
						</Link>
					</li>

					<li>
						<a href="#/featured/sports" className={cls.navbarlinks}>
							{`PALARO`}
						</a>
					</li>

					<li>
						<a onClick={openNav} href="/#/login">
							{`LOGIN`}
						</a>
					</li>

					<li>
						<a onClick={openNav} href="/">
							{`REGISTER`}
						</a>
					</li>
				</ul>
			</div>

			{/* Hamburger Icon */}
			<div className="mobile-nav" style={{ display: isMobile ? 'block' : 'none' }}>
				<FontAwesomeIcon icon={faBars} onClick={openNav} className="hamb-icon" />
			</div>
		</div>
	);
}

export default Navbar;
